import { mapActions } from 'vuex'
import { mapGetters } from 'vuex'
import ReviewSection from '../components/ReviewSection'
const rating = process.client ? require('vue-rate-it') : {}
export default {
  components: {
    ReviewSection,
    StarRating: rating.StarRating
  },
  async asyncData ({ $axios, route }) {
    try {
      let singleProduct = $axios.$get(`${process.env.DEV_BACKEND}/api/products/${route.query.item}`)
      let manyReviews = $axios.$get(`${process.env.DEV_BACKEND}/api/review/${route.query.item}`)
      let shipments = $axios.$post(`${process.env.DEV_BACKEND}/api/payment/shipment`, {
        shipment: 'normal'
      })

      const [productResponse, 
             reviewResponse, 
             shipmentResponse] = await Promise.all([
               singleProduct,
               manyReviews,
               shipments
             ])
            //  console.log('average rateing:\t', productResponse.product.averageRating);
            //  console.log('reviews:\n', reviewResponse);
      return {
        product: productResponse.product,
        reviews: reviewResponse.reviews,
        estimatedDelivery: shipmentResponse.shipment.estimated
      }
    } catch (error) {
      console.log('something went wrong');
      console.log('failed to try\n', error);
    }
  },

  data () {
    return {
      product: {
        category: {},
        owner: {},
        title: '',
        description: '',
        photo: '',
        price: 0,
        stockQuantity: 0,
        reviews: []
      },
      reviews: [],
      estimatedDelivery: {}
    }
  },

  methods: {
    ...mapActions(['addProductToCart']),
    ...mapGetters(['getLog', 'getCity'])
  },
}
